html {
  font-size: 62.5%;
}

body {
  min-width: 320px;
  width: 100%;
  margin: 0;
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  color: #707070;
  overflow-x: hidden;
  line-height: 1.6;
}

image {
  min-width: 100%;
  height: auto;
  vertical-align: middle;
}

h1, h2, h3, h4, h5, h6, p, figure {
  padding: 0;
  margin: 0;
  font-size: 100%;
}

ul, ol {
  list-style-type: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  width: 100%;
}

.l-container {
  padding: 30px 30px;
  text-align: center;
  max-width: 1300px;
  margin: 0 auto;
}
@media screen and (min-width: 768px) {
  .l-container {
    padding: 50px 50px;
  }
}
@media screen and (min-width: 1025px) {
  .l-container {
    padding: 80px 50px;
  }
}

.c-menuCard {
  padding-top: 30px;
  text-align: left;
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 40px;
  justify-content: center;
}
@media screen and (min-width: 768px) {
  .c-menuCard {
    justify-content: center;
  }
  .c-menuCard:last-child {
    justify-content: start;
  }
}
.c-menuCard__item {
  padding: 40px 10px;
  width: 100%;
  max-width: 400px;
}
@media screen and (min-width: 768px) {
  .c-menuCard__item {
    width: 50%;
    padding: 40px 2%;
  }
}
@media screen and (min-width: 1025px) {
  .c-menuCard__item {
    width: 33.3333333333%;
  }
}
.c-menuCard__item__image {
  width: 100%;
  height: 300px;
  object-fit: cover;
  object-position: center;
  display: block;
}
.c-menuCard__item__description {
  padding-bottom: 20px;
  padding: 15px;
}
.c-menuCard__item__description__title {
  font-size: 2rem;
  padding: 10px 0;
}
.c-menuCard__item__description__text {
  height: 50px;
  overflow: hidden;
}
.c-menuCard__item__description__link {
  display: flex;
  justify-content: right;
  align-items: center;
  padding: 15px;
}

.c-mark {
  margin-left: 10px;
  width: 40px;
  height: 8px;
  border-bottom: 1px solid #000;
  border-right: 1px solid #000;
  transform: skew(45deg);
}

.p-hamburger {
  width: 70px;
  height: 60px;
  position: relative;
  position: fixed;
  right: 0;
  z-index: 9999999;
}
@media screen and (min-width: 1025px) {
  .p-hamburger {
    display: none;
  }
}
.p-hamburger::before, .p-hamburger::after {
  content: "";
  width: 30px;
  height: 1.5px;
  background: #707070;
  position: absolute;
  left: calc(50% - 15px);
  display: block;
  transition: transform 0.4s;
}
.p-hamburger::before {
  top: 22px;
}
.p-hamburger::after {
  top: 38px;
}
.p-hamburger > span {
  width: 30px;
  height: 1.5px;
  font-size: 0;
  background: linear-gradient(to right, #707070, #707070) repeat-x #707070;
  background-size: auto 1.5px;
  position: absolute;
  left: calc(50% - 15px);
  top: 50%;
  transition: transform 0.4s;
}
.p-hamburger.is-open::before {
  transform: rotate(45deg);
  top: 28px;
}
.p-hamburger.is-open::after {
  transform: rotate(-45deg);
  top: 28px;
}
.p-hamburger.is-open > span {
  background: none;
}

.p-footer {
  text-align: center;
  padding-bottom: 50px;
}
.p-footer__sns-menu {
  padding-bottom: 30px;
}
.p-footer__sns-menu__icon {
  font-size: 3.5rem;
  padding: 0 5px;
}
@media screen and (min-width: 768px) {
  .p-footer__sns-menu__icon {
    font-size: 5rem;
  }
}
.p-footer__nav {
  display: flex;
  justify-content: center;
  padding-bottom: 50px;
}
.p-footer__nav__item {
  border-right: 1px solid #000;
  padding: 0 10px;
  line-height: 1;
}
.p-footer__nav__item:last-child {
  border: none;
}